"use client";

import { useState } from "react";

export default function Home() {
  const [code, setCode] = useState("");
  const [output, setOutput] = useState("");

  const runCode = async () => {
    setOutput("Running...");

    const res = await fetch("/api/parse", {
      method: "POST",
      body: code
    });

    const data = await res.json();
    setOutput(data.output || data.error);
  };

  return (
    <main className="min-h-screen bg-gray-900 text-white p-6">
      <h1 className="text-3xl font-bold mb-6">Mini Language IDE</h1>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">

        {/* CODE EDITOR */}
        <div className="bg-gray-800 rounded-lg border border-gray-700 p-4">
          <h2 className="text-xl font-semibold mb-2">Code Editor</h2>

          <textarea
            value={code}
            onChange={(e) => setCode(e.target.value)}
            className="w-full h-80 bg-gray-900 text-green-400 p-3 rounded-lg font-mono text-sm outline-none border border-gray-700"
            placeholder="Type your code here..."
          />
          
          <button
            onClick={runCode}
            className="mt-4 px-6 py-2 bg-blue-600 hover:bg-blue-700 rounded-lg font-semibold"
          >
            RUN
          </button>
        </div>

        {/* OUTPUT PANEL */}
        <div className="bg-gray-800 rounded-lg border border-gray-700 p-4">
          <h2 className="text-xl font-semibold mb-2">Output</h2>

          <pre className="w-full h-80 bg-gray-900 text-yellow-300 p-3 rounded-lg overflow-auto font-mono text-sm">
            {output}
          </pre>
        </div>

      </div>
    </main>
  );
}
